.total {
  grid-column: 1/3;
  padding: 2rem;
  font-size: 2rem;
}

.graph {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

.graphItem {
  display: grid;
  align-items: center;
  border-radius: .2rem;
  box-shadow: 0 10px 20px rgba(0, 0, 0, .1);
}

@media (max-width: 40rem) {
  .graph {
    grid-template-columns: 1fr;
  }

  .total {
    grid-column: 1;
  }
  
}
